function hasFirstChildLink(elem: Element | null): boolean {
  if (!elem) return false;
  if (elem && elem.tagName === "A") return true;

  return hasFirstChildLink(elem.firstChild as HTMLElement | null);
}

export function setSkiStyle(): void {
  // [ski-style] Adds "ski-style-link" to li a-tag elements
  document.querySelectorAll(".ski-style ul li").forEach((li: Element): void => {
    if (hasFirstChildLink(li)) {
      li.classList.add("ski-style-link");
    }
  });
}

export async function fetchAPI(
  self: Vue,
  url: string,
  id = "",
  redirect404 = false,
  optionals = {
    origin: true,
    customModals: [0],
    filename: null
  },
  customApiException = false
): Promise<{ ok: boolean; json: any; statusCode: number }> {
  let reqUrl;
  try {
    var origin = self.$root.$data.ApiOrigin;
    if (origin === undefined) {
      origin = "https://test.api.ski.dk/rest/v1/"
    }
    const endpoint = self.$config.endpoint;
    reqUrl = optionals.origin ? origin + endpoint : "";
    reqUrl += url.replace(":id", id);

    const headers = new Headers();
    if (!optionals.filename) {
      headers.append("Content-Type", "application/json");
    }

    const apiJwt = getJwtToken();
    if (apiJwt) {
      headers.append("Authorization", `Bearer ${apiJwt}`);
    }

    const response = await timeout(
      fetch(reqUrl, { headers }),
      60 * 1000 // 60 seconds
    );

    if (!response.ok) throw response;

    setTimeout(setSkiStyle, 1);

    try {
      if (optionals.filename) {
        // Handle binary
        const blob = await response.blob();
        saveAs(blob, `${optionals.filename}`);
        return { ok: true, json: null, statusCode: response.status };
      } else {
        // Handle empty body or comment
        const json = await response.json();
        return { ok: true, json, statusCode: response.status };
      }
    } catch (error) {
      return { ok: true, json: null, statusCode: response.status };
    }
  } catch (err) {
    logger.error("Fetch API - Failed", err);

    if (customApiException) {
      err.json().then((data: any): any => {
        const { value } = data;
        if (value) {
          if (value.type === "profile-inactive") {
            return { ok: false, json: null, statusCode: err.status };
          } else {
            alertException(self, value.detail, value.title);
          }
        }
      });
      return { ok: false, json: null, statusCode: err.status };
    }

    // Checks if redirect to page not found or checks for custom modals to replace the generic one
    if (err.status === 404 && redirect404) {
      window.location.href = "/siden-findes-ikke/";
    } else if (!optionals.customModals.includes(err.status)) {
      const errText = (err.status || err) + " " + (err.statusText || "");
      const msg = baseErrorText.replace("<err>", errText);
      alertMessage(self, msg, reqUrl);
    }

    return { ok: false, json: null, statusCode: err.status };
  }
}

export async function sendToAPI(
  self: Vue,
  url: string,
  id = "",
  httpMethod = "",
  requstBody = {},
  customApiException = false,
  redirect404 = false,
  optionals = {
    origin: true,
    customModals: [0],
    filename: null
  }
): Promise<{ ok: boolean; json: any; statusCode: number }> {
  let reqUrl;
  try {
    const origin = self.$root.$data.ApiOrigin;
    const endpoint = self.$config.endpoint;
    reqUrl = optionals.origin ? origin + endpoint : "";
    reqUrl += url.replace(":id", id);

    const headers = new Headers();
    if (!optionals.filename) {
      headers.append("Content-Type", "application/json");
    }

    const apiJwt = getJwtToken();
    if (apiJwt) {
      headers.append("Authorization", `Bearer ${apiJwt}`);
    }

    const response = await timeout(
      fetch(reqUrl, {
        method: httpMethod,
        headers,
        body: JSON.stringify(requstBody)
      }),
      60 * 1000 // 60 seconds
    );

    if (!response.ok) {
      if (customApiException) {
        const { value } = await response.json();
        alertException(self, value.msg, value.itle);
      }
      throw response
    };

    setTimeout(setSkiStyle, 1);

    try {
      if (optionals.filename) {
        // Handle binary
        const blob = await response.blob();
        saveAs(blob, `${optionals.filename}`);
        return { ok: true, json: null, statusCode: response.status };
      } else {
        // Handle empty body or comment
        const json = await response.json();
        return { ok: true, json, statusCode: response.status };
      }
    } catch (error) {
      return { ok: true, json: null, statusCode: response.status };
    }
  } catch (err) {
    logger.error("Send to API - Failed", err);

    if (customApiException) {
      err.json().then((data: any): any => {
        alertException(self, data.detail, data.title);
      });
      return { ok: false, json: null, statusCode: err.status };
    }

    // Checks if redirect to page not found or checks for custom modals to replace the generic one
    if (err.status === 404 && redirect404) {
      window.location.href = "/siden-findes-ikke/";
    } else if (!optionals.customModals.includes(err.status)) {
      const errText = (err.status || err) + " " + (err.statusText || "");
      const msg = baseErrorText.replace("<err>", errText);
      alertMessage(self, msg, reqUrl);
    }

    return { ok: false, json: null, statusCode: err.status };
  }
}

export function errorHandling(self: Vue, err: any, url: string, id = ""): void {
  if (err.status === 404) {
    window.location.href = "/siden-findes-ikke/";
  } else {
    const errText = (err.status || err) + " " + (err.statusText || "");
    const msg = baseErrorText.replace("<err>", errText);
    var origin = self.$root.$data.ApiOrigin;
    var reqUrl = origin + url
    if (url.toLowerCase().includes("id:")) {
      reqUrl = url.replace("id:", id)
    } else if (url.toLowerCase().includes("?id=")) {
      reqUrl = reqUrl + id;
    }
    alertMessage(self, msg, reqUrl);
  }
}

export function animateCSS(
  element: string,
  animationName: string,
  additional: string[] = [],
  callback: Function | null = null
): void {
  document.querySelectorAll(element).forEach((node): void => {
    node.classList.add("animated", animationName, ...additional);

    const handleAnimationEnd = (): void => {
      node.classList.remove("animated", animationName, ...additional);
      node.removeEventListener("animationend", handleAnimationEnd);

      if (typeof callback === "function") callback();
    };

    node.addEventListener("animationend", handleAnimationEnd);
  });
}

export function formatDate(date?: string): string {
  if (!date) return "";

  // Pattern for UTC format  
  const UTCpattern = new RegExp("^(-?(?:[1-9][0-9]*)?[0-9]{4})-(1[0-2]|0[1-9])-(3[01]|0[1-9]|[12][0-9])T(2[0-3]|[01][0-9]):([0-5][0-9]):([0-5][0-9])(\\.[0-9]+)?(Z)?$");

  const yeardatemonthPattern = new RegExp("^(-?(?:[1-9][0-9]*)?[0-9]{4})-(1[0-2]|0[1-9])-(3[01]|0[1-9]|[12][0-9])");

  if (!UTCpattern.test(date) && !yeardatemonthPattern.test(date)) {
    return date;
  }

  const parsedDate = dayjs.utc(date);

  if (!parsedDate.isValid()) {
    return date
  };
  return parsedDate.local().format("D. MMMM YYYY");
}

export function isDarkColor(theme: string): boolean {
  return ["primary", "light", "dark", "darkGreen", "cherry", "plum", "darkPurple", "lightPurple", "skiGreen"].includes(theme);
}

export function stripHtmlStyle(html: string | null): string | null {
  if (html == null) return null;
  return dompurify.sanitize(html, {
    ADD_TAGS: ["iframe"],
    FORBID_ATTR: ["style"]
  });
}

export function compareValues(key: any, order = "asc"): any {
  return function innerSort(a: any, b: any): number {
    if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
      // property doesn't exist on either object
      return 0;
    }

    const varA = typeof a[key] === "string" ? a[key].toUpperCase() : a[key];
    const varB = typeof b[key] === "string" ? b[key].toUpperCase() : b[key];

    let comparison = 0;
    if (varA > varB) {
      comparison = 1;
    } else if (varA < varB) {
      comparison = -1;
    }
    return order === "desc" ? comparison * -1 : comparison;
  };
}

export function groupBy(xs: any[], key: string): any {
  return xs.reduce(function (rv, x): {} {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
}

export function getFileTypeFromExtension(filename: string): string {
  if (!filename) return "";

  // TODO: Reaserch package that can do this better
  const fileTypeDictionary: Dictionary<string> = {
    docx: "Word",
    pdf: "PDF",
    xls: "Excel",
    xlsx: "Excel",
    zip: "ZIP",
    ppt: "Powerpoint",
    pptx: "Powerpoint"
  };

  const filenameSplitted = filename.split(".");

  // Returns empty string if no "." in filename
  if (filenameSplitted.length < 2) return "";

  // Returns empty string if no match with extension and fileTypeDictionary
  const extension = filenameSplitted[filenameSplitted.length - 1].toLowerCase();
  if (fileTypeDictionary[extension] === undefined) return "";

  return fileTypeDictionary[extension];
}

export function getDescription(self: Vue, titleKey: string): string {
  const { title, content } = self.$config.agreement.descriptions;

  const descriptions = self.$root.$data.itemDescriptions;
  if (!descriptions) return "";

  const descriptionItem = descriptions.find(
    (item: any): boolean => item[title] === titleKey
  );

  if (descriptionItem) return descriptionItem[content];

  return "";
}

interface Item {
  url: string;
  whiteHeader?: boolean;
  visible?: boolean;
  items?: Item[];
}

// Find recursive url path match and set whiteheader thereby
function pathWhiteHeaderHelper(
  path: string,
  item: Item
): boolean | undefined | null {
  let whiteHeader;
  const { items = [] } = item;

  items.forEach((i: Item): void => {
    const wh = pathWhiteHeaderHelper(path, i);

    if (typeof wh !== "undefined") {
      whiteHeader = wh;
    }
  });

  if (path === item.url || whiteHeader === null) {
    if (typeof item.whiteHeader === "undefined") return null;
    return item.whiteHeader;
  }

  return whiteHeader;
}

export function pathWhiteHeader(
  path: string,
  item: Item
): boolean | undefined | null {
  if (path.slice(-1) !== "/") path += "/";

  const urls = (path.match(/\//g) || []).map((_, idx): string =>
    path.replace(new RegExp(`([^/]*/?){${idx}}$`, "g"), "")
  );

  for (let i = 0; i < urls.length; i++) {
    const url = urls[i];
    const wh = pathWhiteHeaderHelper(url, item);
    if (typeof wh !== "undefined") {
      return wh;
    }
  }
  return undefined;
}

export const ROLE_CUSTOMER = "kunde";
export const ROLE_SUPPLIER = "leverandoer";

export function hasRole(user: User | null, role: string): boolean {
  if (!user) return false;
  const ROLE_PREFIX = "OrganizationsOrg Users ";
  return user.Roles.includes(ROLE_PREFIX + role);
}

export function getValue(keys: string | string[], obj: any): any {
  function mapper(v: any, k: string): any {
    if (!v) return null;
    if (typeof v === "string") return v;
    if (Array.isArray(v)) return v.map((i): any => mapper(i, k));

    return v[k];
  }

  if (Array.isArray(keys)) {
    const result = keys.reduce(mapper, obj);
    return Array.isArray(result) ? result.join(", ") : result;
  } else {
    return obj[keys];
  }
}

export function someMatch(str: string, items: string[]): boolean {
  return items.some((item): boolean =>
    new RegExp("^" + str.replace(/\*/g, ".*") + "$").test(item)
  );
}

export function downloadTableCSV(tableId: string, tableName: string): void {
  // Select rows from tableId
  const rows = document.querySelectorAll("table#" + tableId + " tr");
  // Construct csv
  const csv = [];
  for (let i = 0; i < rows.length; i++) {
    const row = [];
    const cols = rows[i].querySelectorAll<HTMLElement>("td, th");
    for (let j = 0; j < cols.length; j++) {
      // Clean innertext to remove multiple spaces and jumpline (break csv)
      let data = cols[j].innerText
        .replace(/(\r\n|\n|\r)/gm, "")
        .replace(/(\s\s)/gm, " ");
      // Escape double-quote with double-double-quote
      data = data.replace(/"/g, '""');
      // Push escaped string
      row.push('"' + data + '"');
    }
    csv.push(row.join(";"));
  }
  const csvString = csv.join("\n");
  // Download it
  const filename = tableName + " " + new Date().toLocaleDateString() + ".csv";
  const link = document.createElement("a");
  link.style.display = "none";
  link.setAttribute("target", "_blank");
  link.setAttribute(
    "href",
    "data:text/csv;charset=utf-8,%EF%BB%BF" + encodeURIComponent(csvString)
  );
  link.setAttribute("download", filename);
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}

export function convertCrmStatusToSkiStatus(agreement: any): void {
  if (
    agreement.overallStatusName === "Aktiv" ||
    agreement.overallStatusName === "Udløbet"
  ) {
    if (dayjs(agreement.expirationDateUtc).isBefore(dayjs().add(-1, "day").utc())) {
      agreement.type = "Tidligere aftale";
      agreement.variant = "danger";
    } else {
      agreement.type = "Aftale";
      agreement.variant = "aftale";
    }
  } else if (agreement.overallStatusName === "Kommende") {
    agreement.type = "Udbud";
    agreement.variant = "udbud";
  }
}

export function saveValueToLocalStorage(name: string, val: object): void {
  var jsonVal;
  if (!!name && !!val) {
    try {
      if (name === "subscriptions") {
        var expires = dayjs().add(10, "minute");
        var jsonValDate = JSON.stringify(expires);
        localStorage.setItem("subScriptionsExpireDate", jsonValDate);
        var jsonValSubcriptions = JSON.stringify(val);
        localStorage.setItem(name, jsonValSubcriptions);
      } else {
        jsonVal = JSON.stringify(val);
        localStorage.setItem(name, jsonVal);
      }
    } catch (err) {
      console.log(err);
    }

  }
}

export function toJSON(data: any): any {
  data = typeof data === 'object' ? data : {};
  if (Array.isArray(data.skiAgreementlist)) {
    data.skiAgreementlist = [];
    for (const item of data.skiAgreementlist)
      data.skiAgreementlist.push(item.toJSON());
  }
  if (Array.isArray(data.skiGroups)) {
    data.skiGroups = [];
    for (const item of data.skiGroups)
      data.skiGroups.push(item.toJSON());
  }
  if (Array.isArray(data.skiSourcingprograms)) {
    data.skiSourcingprograms = [];
    for (const item of data.skiSourcingprograms)
      data.skiSourcingprograms.push(item.toJSON());
  }
  if (Array.isArray(data.skiOverallStatuses)) {
    data.skiOverallStatuses = [];
    for (const item of data.skiOverallStatuses)
      data.skiOverallStatuses.push(item.toJSON());
  }
  return data;
}

export function loadValueToLocalStorage(name: string): any {
  if (name) {
    const encodedVal = localStorage.getItem(name);
    if (!encodedVal) {
      return null;
    }
    if (isJson(encodedVal)) {
      const val = JSON.parse(encodedVal as string);
      return val;
    }
  }
  return null;
}

export function saveCurrentSubscriptions(subscriptions: object): void {
  if (getUserData()) {
    saveValueToLocalStorage("subscriptions", subscriptions);
  }
}

export function loadCurrentSubscriptions(): any {
  if (getUserData()) {
    return loadValueToLocalStorage("subscriptions");
  }
  return null;
}

export function loadCurrentSubscriptionDate(): any {
  if (getUserData()) {
    return loadValueToLocalStorage("subScriptionsExpireDate");
  }
  return null;
}

export function convertSubAgreement(subAgreement: any, agreement: any): any {
  return {
    "agreementId": subAgreement.subAgreementId,
    "overallStatusName": subAgreement.overallStatusName,
    "displayNameLong": subAgreement.displayNameLong,
    "sourcingPrograms": agreement.sourcingPrograms,
    "group": agreement.group
  }
}

function GetCalendarFields(agreements: any[]) {
  const calendars = agreements
    .flatMap(agreement => agreement.calendars || [] as CalendarDbDto[])
    .filter(calendar => calendar.includeInExcel);

  // remove duplicates
  return calendars.reduce((list, calendar) => {
    if (!list.includes(calendar.skiName || '')) {
      list.push(calendar.skiName || '');
    }
    return list;
  }, [] as string[]);
}

export function downloadCompleteSkiTable(self: Vue, tableName: string, fields: Field[], agreements: any[]): void {
  const csv = [];
  const headerRow = [] as string[];
  const isUserCustomer = hasRole(getUserData(), ROLE_CUSTOMER);
  const columns = [...fields];
  let printableFields = [] as Field[];

  if (!isUserCustomer) {
    printableFields = columns.filter((column: Field) => column.key !== 'highestPriorityCustomerApplication');
  } else {
    printableFields = [...columns];
  }

  const extraFields = GetCalendarFields(agreements);

  // Adding two extra filter criterias manually since they not exist in the original fields
  printableFields.push({ label: "Aftaleområde", key: "group" });
  printableFields.push({ label: "Indkøbsprogram", key: "sourcingPrograms" });

  printableFields.forEach((field): void => {
    if (field.extraFields) {
      extraFields.forEach((f: string): void => {
        headerRow.push(f);
      });
    } else {
      headerRow.push(field.label)
    }
  });
  csv.push(headerRow.join(";"))

  agreements.forEach((agreement): void => {
    const row = [] as any[];
    printableFields.forEach((field): void => {
      if (field.extraFields) {
        for (const calendarField of extraFields) {
          const calendar = agreement.calendars
            .sort((a: CalendarDbDto, b: CalendarDbDto) =>
              (new Date(b.skiStartdate || 0)).getTime() - new Date(a.skiStartdate || 0).getTime())
            .find((x: CalendarDbDto) => x.skiName === calendarField);
          if (calendar && calendar.skiAlternativeenddate) {
            row.push(`"${calendar.skiAlternativeenddate}"`);
          } else {
            row.push(" ");
          }
        }
      }
      else if (field.key === self.$config.agreement.application.activeApplication && isUserCustomer) {
        const applicationField = agreement[self.$config.agreement.application.activeApplication]
        const applicationText = applicationField !== null
          ? applicationField[self.$config.agreement.application.label]
          : "-";
        row.push(`"${applicationText}"`);
      }
      else if (field.key.toLowerCase().includes("expectedintoforcestartdateutc")) {
        if (agreement.expectedIntoForceAlternateDisplayDate) {
          row.push(`"${agreement.expectedIntoForceAlternateDisplayDate}"`);
        } else {
          row.push(`"${formatDate(agreement[field.key])}"`);
        }
      }
      else if (field.key.toLowerCase().includes("date") && !field.key.toLowerCase().includes("highestprioritycustomerapplication")) {
        row.push(`"${formatDate(agreement[field.key])}"`);
      }
      else if (field.key.toLowerCase().includes("highestprioritycustomerapplication") && isUserCustomer) {
        row.push(`"${formatDate(agreement.highestPriorityCustomerApplication[(field.key.substring(field.key.indexOf('.') + 1))])}"`);
      }
      else if (field.key.toLowerCase().includes("skinextmilestone")) {
        if (agreement.skiNextMilestone) {
          if (agreement.skiNextMilestone.skiAlternavtivenenddate) {
            row.push(`"${agreement.skiNextMilestone.name} ${agreement.skiNextMilestone.skiAlternavtivenenddate}"`);
          } else {
            row.push(`"${agreement.skiNextMilestone.name} ${formatDate(agreement.skiNextMilestone.skiStartdateUtc)}"`);
          }
        } else {
          row.push(" ")
        }
      }
      else if (field.key.toLowerCase().includes("group")) {
        row.push(`"${agreement.group.charAt(0).toUpperCase()}${agreement.group.slice(1).toLowerCase()}"`);
      }
      else if (field.key.toLowerCase().includes("sourcingprograms")) {
        const sourcingProgramList = (agreement.sourcingPrograms || []).map((x: any) => x.name || x);
        row.push(`"${sourcingProgramList.join(', ')}"`);
      }
      else {
        row.push(`"${agreement[field.key] ? agreement[field.key] : ""}"`);
      }
    });



    csv.push(row.join(";"));
  });

  const csvString = csv.join("\n");
  // Download it
  const filename = tableName + " " + new Date().toLocaleDateString() + ".csv";
  const link = document.createElement("a");
  link.style.display = "none";
  link.setAttribute("target", "_blank");
  link.setAttribute(
    "href",
    "data:text/csv;charset=utf-8,%EF%BB%BF" + encodeURIComponent(csvString)
  );
  link.setAttribute("download", filename);
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
}

export function saveUpdateId(statusId: string): void {
  if (statusId) {
    saveValueToLocalStorage("statusUpdateId", { "id": statusId });
  }
}

export function loadUpdateId(): any {
  return loadValueToLocalStorage("statusUpdateId");
}

export function deleteUpdateId(): void {
  localStorage.removeItem("statusUpdateId");
}

export function getCustomerApplication(self: Vue, customerApplication: any, customerApplicationPriorities: any): any {
  var priorityObj = customerApplicationPriorities.find((ele: any): any => ele.type === customerApplication[self.$config.agreement.application.type].toLowerCase());
  customerApplication[self.$config.agreement.application.anvendelse] = priorityObj.priority;
  return customerApplication;
}

export function highestCustomerApplication(self: Vue, customerApplicationList: any, priorities: any | null): any {
  var customerApplicationPriorities = priorities || self.$config.agreement.application.priorities;
  if (!customerApplicationList || !self) {
    return null;
  }
  if (!Array.isArray(customerApplicationList)) {
    return getCustomerApplication(self, customerApplicationList, customerApplicationPriorities);
  }
  if (customerApplicationList.length === 1) {
    return getCustomerApplication(self, customerApplicationList[0], customerApplicationPriorities);
  }
  var returnCustomerApplication;
  var priority = 50;
  for (var i = 0; i < customerApplicationList.length; i++) {
    var temp = customerApplicationList[i];
    var priorityObj = customerApplicationPriorities.find((ele: any): any => ele.type === temp[self.$config.agreement.application.type].toLowerCase());
    if (priorityObj && priorityObj.priority < priority) {
      priority = priorityObj.priority;
      returnCustomerApplication = temp;
      returnCustomerApplication[self.$config.agreement.application.anvendelse] = priority;
    }
  }
  return returnCustomerApplication;
}

export function setHighestPriorityCustomerApplication(self: Vue, agreements: any, applicationKeys: any = null): void {
  var priorities = null;
  if (applicationKeys) {
    priorities = self.$config.agreement.application.priorities.filter((priority: any): any => applicationKeys.some((key: any): any => priority.type === key.toLowerCase()));
  }
  if (Array.isArray(agreements)) {
    for (var i = 0; i < agreements.length; i++) {
      var agreement = agreements[i];
      agreement[self.$config.agreement.application.activeApplication] = highestCustomerApplication(self, agreement.customerApplication, priorities);
    }
  }
  else {
    agreements[self.$config.agreement.application.activeApplication] = highestCustomerApplication(self, agreements.customerApplication, priorities);
  }
}

export function downloadExcelSheet(self: Vue, filteredAgreements: AgreementItem[], fields: Field[], csvFilename: string): void {
  setHighestPriorityCustomerApplication(self, filteredAgreements) 
      if(filteredAgreements !== null && fields != null){
        const filteredFields = [...fields].filter(field => field.toString() !== "index");
        downloadCompleteSkiTable(self, csvFilename, filteredFields, filteredAgreements);
      }
}

