







































import Vue from "vue";
import qs from "qs";
import dayjs from "dayjs";

import SkiDefaultButton from "@/lib/components/SkiDefaultButton.vue";
import {
  qsParams,
  getUserData,
  isLoggedIn,
  hasRole,
  ROLE_CUSTOMER,
  ROLE_SUPPLIER,
  deleteCurrentSubscriptions,
  getValue
} from "@/lib/utilities";

export default Vue.extend({
  components: { SkiDefaultButton },
  props: {
    loginUrl: { type: String, required: true },
    loggedInCustomerUrl: { type: String, required: true },
    loggedInSupplierUrl: { type: String, required: true }
  },
  data() {
    return {
      query: qsParams(),
      failsafeTimeout: 0
    };
  },
  created() {
    const { redirect, iframe, jwt } = this.query;
    const newWebToken = this.query["web-token"];

    // Don't do anything if it is an iframe
    if (iframe) return;

    this.$root.$on("api-token", () => this.redirectLoggedIn(redirect));

    if (!isLoggedIn() && !jwt) {
      if (newWebToken) {
        // SET SKI AUTH COOKIE
        const date = dayjs.utc().add(1, "hour");
        document.cookie =
          "SKIAuth=; path=/; domain=.ski.dk; secure; expires=" +
          date.toString();

        localStorage.setItem("web-token", newWebToken);
        this.$root.$emit("web-token", newWebToken);

        // Failsafe fetching jwt token without iframe
        this.failsafeTimeout = window.setTimeout(() => {
          let url = this.$root.$data.ApiOrigin;
          url += "identity/authenticate/";
          url += `?wctx=${window.location.origin + window.location.pathname}`;
          if (redirect) url += `?redirect=${redirect}`;

          window.location.href = url;
        }, 3 * 1000); // 3 seconds
      } else {
        const qsString = qs.stringify(this.query, { addQueryPrefix: true });
        window.location.href = this.loginUrl + qsString;
      }
    }
  },
  mounted() {
    const { redirect, iframe, jwt } = this.query;

    // Don't do anything if it is an iframe
    if (iframe) return;

    if (isLoggedIn()) {
      this.redirectLoggedIn(redirect);
    } else if (jwt) {
      // Failsafe jwt (api-token)
      const date = dayjs().add(1, "hour");
      const value = JSON.stringify({ jwt, expires: date.toISOString() });
      localStorage.setItem("api-token", value);

      this.redirectLoggedIn(redirect);
    } else {
      this.$bvModal.show("bv-modal-redirect");
    }
  },
  methods: {
    redirectLoggedIn(redirect: string) {
      const usr = getUserData();
      if (!usr) return;
      var redirectUrl = redirect;
      var first = true;
      for(var element in this.query) {
        if(element !== "redirect" && element !== "iframe" && element !== "jwt") { 
          var queryParam = getValue(element, this.query);
          if(first) {
            first = false;
            redirectUrl += `?${element}=${queryParam}`;
          }
          else {
            redirectUrl += `&${element}=${queryParam}`;
          }
        }
      }
      if (redirectUrl) return (window.location.href = redirectUrl);
      deleteCurrentSubscriptions();
      const isCustomer = hasRole(usr, ROLE_CUSTOMER);
      const isSupplier = hasRole(usr, ROLE_SUPPLIER);

      if (isCustomer && isSupplier) {
        this.$bvModal.show("bv-modal-select-role");
        clearTimeout(this.failsafeTimeout);
      } else {
        window.location.href = isSupplier
          ? this.loggedInSupplierUrl
          : this.loggedInCustomerUrl;
        this.$bvModal.show("bv-modal-redirect");
      }
    }
  }
});
