

















import { getUserData, isLoggedIn } from "@/lib/utilities";
import Vue from "vue";
  
interface Data {
  showWidget: boolean;
}

export default Vue.extend({
  components: {
  },
  props: {
    title: { type: String, required: true },
    bodyText: { type: String, required: true },
    active: { type: Boolean, required: true },
    allowedUsers: { type: String, required: true},
    stylesheetLink: { type: String, required: true },
    widgetLink: { type: String, required: true },
  },  
  data(): Data {
      return {
          showWidget: false
      };
  },
  created() {
    window.history.scrollRestoration = "manual";
    const orgList = this.allowedUsers.replace(/\s/g, "").split(",");

    if (isLoggedIn()) {
      const usr = getUserData();
      if (!usr) return;

      this.showWidget = this.active === true && !!(orgList.includes(usr.OrgId) || this.allowedUsers === "");
      this.$parent.$emit("show-hearing-widget", this.showWidget);
    }
  },
  destroyed() {
    window.history.scrollRestoration = "auto";
  }
});
