// @ts-nocheck
import {SKIAgreementRequest, FaqListViewDto, ApiService, StatusUpdateViewDto, StatusupdateListViewDto, AgreementDto, CustomerListApplicationViewDto, DescriptionListViewDto, FileResponse, OrganizationListViewDto, AgreementListViewDtoWrapper,  StatusUpdatesRequest} from "@/lib/APIService";
import { ApiServiceFactory } from "@/lib/APIServiceFactory";

/* eslint-disable @typescript-eslint/explicit-member-accessibility */

/* eslint-disable @typescript-eslint/no-parameter-properties */

/* eslint-disable no-useless-constructor */

// base class
 class BaseRepository {
    protected get api(): ApiService {
        return this.parent.api;
    }
    
    public constructor(readonly parent: BaseRepository) {
    }
}

export class AgreementRepository extends BaseRepository {
   
    public constructor(parent: BaseRepository) {
        super(parent);
    }

    public getFAQAgreement(agreementId: string): Promise<FaqListViewDto[]> {
        return this.api.faq(agreementId);
    }

    public getStatusUpdatesAgreement(agreementId: string): Promise<StatusupdateListViewDto> {
        return this.api.statusUpdate(agreementId);
    }

    public get(agreementID: string): Promise<AgreementDto> {
        return this.api.agreement(agreementID);
    }

    public getAllAgreements(status: string | undefined): Promise<AgreementListViewDtoWrapper> {
        return this.api.agreement2(status);
    }

    public getCustomers(agreementID: string): Promise<CustomerListApplicationViewDto[]> {
        return this.api.customer(agreementID);
    }

    public getDescription(agreementId: string): Promise<DescriptionListViewDto[]> {
        return this.api.description(agreementId);
    }
}

export class OrganizationtRepositoryID extends BaseRepository {
    // @ts-ignore
    constructor(parent: BaseRepository) {
        super(parent);
    }

    public get(orgID: string): Promise<OrganizationListViewDto> {
        return this.api.organization(orgID);
    }
}

export class StatusUpdateRepository extends BaseRepository {
    // @ts-ignore
    constructor(parent: BaseRepository) {
        super(parent);
    }

    public getStatusUpdatesForTheUser(id: string): Promise<AgreementListViewDtoWrapper> {
        return this.api.statusUpdateSubscriptionGET(id);
    }

    public getStatus(id: string): Promise<void> {
        return this.api.task(id, undefined);
    }

    public putStatus(id: string, subscribtions:  StatusUpdatesRequest):  Promise<void> {
        return this.api.statusUpdateSubscriptionPUT(id, subscribtions);
    }

    public postStatus(id: string, subscribtions: SKIAgreementRequest): Promise<void> {
        return this.api.statusUpdateSubscriptionPOST(id, subscribtions);
    }

    public getStatusUpdateList(id: string): Promise<StatusupdateListViewDto> {
        return this.api.statusUpdate(id);
    }

    public getStatusUpdate(id: string): Promise<StatusUpdateViewDto> {
        return this.api.statusUpdate2(id);
    }
}

export class DocumentRepository extends BaseRepository {
    // @ts-ignore
    constructor(parent: BaseRepository) {
        super(parent);
    }
    
    public getDocument(id: string): Promise<FileResponse> {
        return this.api.document(id);
    }
}

// the one and only used by business logic
export default class SKIRepository extends BaseRepository {
    private readonly theApi = ApiServiceFactory.create();

    constructor() {
        super(undefined as any as BaseRepository);
    }

    protected get api(): ApiService {
        return this.theApi;
    }

   public get AgreementsRepository(): any {
        return new AgreementRepository(this);
    } 

   public get organizationID(): any {
        return new OrganizationtRepositoryID(this);
    }  

    public get StatusUpdateRepository(): any {
        return new StatusUpdateRepository(this);
    }

    public get DocumentRepository(): any {
        return new DocumentRepository(this);
    }
}