




























import Vue from "vue";
import SkiImage from "@/lib/components/SkiImage.vue";

export default Vue.extend({
  components: { SkiImage },
  props: {
    role: { type: String, required: false, default: null },
    roleDescription: { type: String, required: false, default: null },
    name: { type: String, required: true },
    title: { type: String, required: false, default: null },
    email: { type: String, required: false, default: null },
    phone: { type: String, required: false, default: null },
    photoUrl: { type: String, required: false, default: null },
  },
  data: () => ({
    photoUrlValid: false
  }),
  watch: {
    photoUrl: {
      handler(newVal: string) {
        if (!newVal) {
          this.photoUrlValid = false;
          return;
        }
        const img = new Image();
        img.onload = () => {
          this.photoUrlValid = true;
        };
        img.onerror = () => {
          this.photoUrlValid = false;
        };
        img.src = newVal || "";
      },
      immediate: true
    }
  }
});
