





































































import Vue from "vue";

import PrintButton from "@/lib/components/PrintButton.vue";
import ContactCard from "@/lib/components/ContactCard.vue";
import {isDarkColor, compareValues } from "@/lib/utilities";
import SkiDropDown from "@/lib/components/SkiDropDown.vue";
import {ApiService}  from '@/lib/APIService';

interface Data {
  item: {} | null;
  departments: any[];
  employeeOverview: any[];
  filteredEmployeeOverview: any[];
  selectedDepartment: string;
}

export default Vue.extend({
  components: {
    ContactCard,
    PrintButton,
    SkiDropDown
  },
  props: {
    title: { type: String, required: true },
    bgVariant: { type: String, required: true },
    printVariant: { type: String, required: true },
    filterVariant: { type: String, required: true }
  },
  data(): Data {
    return {
      item: null,
      departments: [],
      employeeOverview: [],
      filteredEmployeeOverview: [],
      selectedDepartment: this.$config.employee.departmentLabel
    };
  },
  watch: {
    selectedDepartment() {
      if (this.selectedDepartment === this.$config.employee.departmentLabel) {
        this.filteredEmployeeOverview = this.employeeOverview;
      } else {
        this.filteredEmployeeOverview = [
          this.employeeOverview.find(
            dep =>
              dep[this.$config.employee.departments.title] ===
              this.selectedDepartment
          )
        ];
      }
    }
  },
  created() {
    this.fetchAPIData();
  },
  methods: {
    isDarkColor,
    async fetchAPIData() {
      var apiService = new ApiService();
      var employees = await apiService.employee2();
      if(employees != null){
        this.item = employees
      }
      this.departmentEmployeeArray(this.item); 
    },
    departmentEmployeeArray(employeeData: any) {
      const apiEmployees: any[] = employeeData.skiEmployeesList;
      const apiDepartments: any[] = employeeData.skiTeamlist;

      // Defining and sorting complete employee overview list
      var employeeByDepartmentSorted = apiDepartments.map(dep => {
        var filteredEmployees = apiEmployees.filter(
          emp => emp.team === dep.skiName
        );

        var fiteredByPrimarySortKey = filteredEmployees
          .filter(emp => emp[this.$config.employee.sortKeys[0]])
          .concat()
          .sort(compareValues(this.$config.employee.sortKeys[0]));
        var fiteredBySecondarySortKey = filteredEmployees
          .filter(emp => !emp[this.$config.employee.sortKeys[0]])
          .concat()
          .sort(compareValues(this.$config.employee.sortKeys[1]));

        dep.employees = fiteredByPrimarySortKey.concat(
          fiteredBySecondarySortKey
        );

        return dep;
      });

      // Filtering out departments without employees
      this.employeeOverview = employeeByDepartmentSorted.filter(
        dep => dep.employees.length
      );
      this.departments = employeeByDepartmentSorted
        .filter(dep => dep.employees.length)
        .map(dep => dep.skiName);

      // Setting initial filter (All)
      this.filteredEmployeeOverview = this.employeeOverview;
    }
  }
});
