


























import Vue from "vue";
import Profile from "@/lib/assets/icon/profile.svg";
import { logout, isLoggedIn, loginAndRedirect, stripHtmlStyle } from "@/lib/utilities";

export default Vue.extend({
  name: "Topbar",
  components: {
    Profile
  },
  props: {
    variant: { type: String, required: true },
    mySkiLabel: { type: String, required: true },
    mySkiUrl: { type: String, required: true },
    username: { type: String, default: null }
  },
  data:function(){
    return {
      currentRoute: window.location.href
    }
  },
  computed: {
    loggedInUserLabel() : string {
      const label = `<b> ${this.username}</b> (${this.$root.$data.logoutLabel})`; 
      return label;
    },
    logoutRoute(): string {
      if (this.currentRoute.includes(this.mySkiUrl)){
        return "/";
      } else {
        return this.currentRoute;
      }
    }
  },
  mounted:function(){
    this.underLineMySkiWhileVisited()
  },
  methods: { 
    isLoggedIn,
    logout,
    loginAndRedirect,
    stripHtmlStyle,
    underLineMySkiWhileVisited: function(){
      const mySkiLink = document.getElementsByClassName("my-ski-link");
      if (mySkiLink != null){
        if (this.currentRoute.includes(this.mySkiUrl)){
          for (let i = 0; i < mySkiLink.length; i++){
            mySkiLink[i].getElementsByTagName("span")[0].classList.add("active-link");
          }
        }
        else {
          for (let i = 0; i < mySkiLink.length; i++){
            mySkiLink[i].getElementsByTagName("span")[0].classList.remove("active-link");
          }
        }
      }
    }
  }
});
