































import Vue from "vue";
import SKIRepository from "@/lib/SKIRepository"
import InfoBox from "@/lib/components/InfoBox.vue"
import { 
  errorHandling,
  qsParams, 
  isEmpty, 
  setHighestPriorityCustomerApplication, 
  isLoggedIn, 
  loadValueToLocalStorage,
  removeValueFromLocalStorage } from "@/lib/utilities";

interface Data {
  id: string;
  item: {} | null;
  agreementType: string;
  infoDescription: string
}

export default Vue.extend({
  components: { InfoBox },
  data(): Data {
    return {
      id: "",
      item: null,
      agreementType: "",
      infoDescription: ""
    };
  },
  created() {
    this.id = qsParams().id;
    this.fetchAndSetAgreementAPIData().then(() => this.scrollToElement());
  },
  methods: {
    async fetchAndSetAgreementAPIData() {
    var skiRepository = new SKIRepository();

    try{
      var agreement = await skiRepository.AgreementsRepository.get(this.id)
      var agreementDescriptions = await skiRepository.AgreementsRepository.getDescription(this.id) ;
      this.$root.$data.itemDescriptions = agreementDescriptions
      if (this.redirectToTenderPage(agreement)) return;
        var iteAgreement = agreement;
        if(iteAgreement) {
          if(isLoggedIn()) {
            setHighestPriorityCustomerApplication(this, iteAgreement);
          }
          else {
            iteAgreement[this.$config.agreement.application.activeApplication] = null;
          }
        }

      var infoDescriptionRaw = agreementDescriptions
      .find((x: { agreementDescriptionName: string; }) => x.agreementDescriptionName === "Særlig information om aftalen");
      this.infoDescription = infoDescriptionRaw !== undefined ? infoDescriptionRaw.agreementDescriptionText : "";

        this.item = iteAgreement;
        this.$root.$data.item = this.item;
        this.setAgreementType(this.item);
    } catch(err) {
      errorHandling(this, err, "/agreement/{id}",this.id );
    }
    },
    redirectToTenderPage(response: any): boolean {
      var { statusField, tenderStatus, tenderPage } = {
        "statusField": "overallStatusName",
        "tenderStatus": "Kommende",
        "tenderPage": "/udbud/se-udbud/"
      };
      
      if (response[statusField] === tenderStatus) {
        window.location.href = tenderPage + "?id=" + this.id;
        return true;
      }
      return false;
    },
    setAgreementType(item: any) {
      if (item === null) return;

      // TODO: FIX This should be handled by backend API
      if (item.mainAgreementName) {
        this.$root.$data.agreementType = this.agreementType = "subAgreement";
      } else if (!isEmpty(item.subAgreement)) {
        this.$root.$data.agreementType = this.agreementType =
          "mainAgreementWithSubAgreements";
      } else {
        this.$root.$data.agreementType = this.agreementType = "mainAgreement";
      }
    },
    shouldRender(component: string) {
      let hidden: string[] = [];
      switch (this.agreementType) {
        case "mainAgreement":
          hidden = ["agreement-item-sub-agreements"];
          break;
        case "mainAgreementWithSubAgreements":
          hidden = ["timeline", "status"];
          break;
        case "subAgreement":
          hidden = ["agreement-item-sub-agreements"];
          break;
      }
      if (hidden.includes(component)) {
        return false;
      }

      return true;
    },
    scrollToElement(){
      const key = "agreementDocuments";
        var value = loadValueToLocalStorage(key);
        if (value){
            var element = document.getElementById(value.agreementDocuments);
            if (element){
              element.scrollIntoView({
                block: 'start',
                behavior: 'smooth'
              });
            }           
          removeValueFromLocalStorage(key);
        }
    }
  }
});
