
































































































import Vue from "vue";

import Burger from "@/lib/assets/icon/burger.svg";
import Close from "@/lib/assets/icon/close.svg";
import Search from "@/lib/assets/icon/search.svg";

import {
  isLoggedIn,
  logout,
  qsParams,
  loginAndRedirect
} from "@/lib/utilities";

interface Data {
  searchQuery: string;
  showCollapse: boolean;
  currentRoute: string;
}

export default Vue.extend({
  components: {
    Burger,
    Close,
    Search
  },
  props: {
    variant: { type: String, required: true },
    type: { type: String, required: true },
    menu: { type: Array, required: true },
    mySkiLabel: { type: String, required: true },
    mySkiUrl: { type: String, required: true },
    loginLabel: { type: String, required: true },
    logoutLabel: { type: String, required: true },
    logoutUrl: { type: String, required: true },
    menuLabel: { type: String, required: true },
    searchLabel: { type: String, required: true },
    searchUrl: { type: String, required: true },
    logo: { type: String, required: true },
    logoUrl: { type: String, required: true },
    logoAltText: { type: String, required: true },
    username: { type: String, default: null }
  },
  data(): Data {
    return {
      searchQuery: qsParams().query || "",
      showCollapse: false,
      currentRoute: window.location.href
    };
  },
  computed: {
    isWhite() {
      return this.variant === "light";
    },
    logoutRoute(): string {
      if (this.currentRoute.includes(this.mySkiUrl)) {
        return "/";
      } else {
        return this.currentRoute;
      }
    }
  },
  methods: {
    isLoggedIn,
    logout,
    loginAndRedirect,
    onSearch(evt: Event) {
      evt.preventDefault();
      window.location.href = this.searchUrl + "?query=" + this.searchQuery;
    }
  }
});
