/* eslint-disable */
import Axios, { AxiosInstance, AxiosRequestConfig } from "axios";
import axiosRetry from "axios-retry";
import { ApiService as Client } from "@/lib/APIService";
import { getJwtToken } from "@/lib/utilities"

export class ApiServiceFactory {

    public static readonly axios = ApiServiceFactory.createAxios();
    public static getBaseUrl() {
        const url = window.location.href;

        if (url.includes("localhost")) {
            return "https://ski-dev-apim.azure-api.net/rest/v1";
        } else if (url.includes("alpha")){
            return "https://ski-test-apim.azure-api.net/rest/v1";
        } else if (url.includes("beta")) {
            return "https://ski-staging-apim.azure-api.net/rest/v1";
        } else {
            return "https://api.ski.dk/rest/v1/";
        }

    }
    public static create(): Client {
        return new Client(this.getBaseUrl(), ApiServiceFactory.axios);
    }

    private static createAxios(): AxiosInstance {
        const config: AxiosRequestConfig = {
            withCredentials: true,
        };

        const inst = Axios.create();
        const intcp = inst.interceptors;
        intcp.request.use(cfg => {
            const token = getJwtToken();
            if (token) {
                cfg.headers["Authorization"] = `Bearer ${token}`;
            }
            return cfg;
        }, err => {
            return Promise.reject(err);
        });
        intcp.request.use(cfg => {
            // Adds a different query parameter depending on whether a user is logged in or not. This creates separate caches for loggen users and public users 
            const url = new URL(cfg.url || "");
            url.searchParams.append('v', getJwtToken() ? "1" : "0");
            cfg.url = url.toString();
            return cfg;
        });
        intcp.response.use(
            response => response,
            error => error.response,
            // NSwag generated process code use the response object, not the error object returned by Axios
        );

        axiosRetry(inst, {
            retries: 3,
            retryDelay: axiosRetry.exponentialDelay,
            retryCondition: axiosRetry.isRetryableError,
        });
        return inst;
    }
}