











import Vue from "vue";
import dayjs from "dayjs";

import { logger, qsParams, alertMessage } from "@/lib/utilities";

export default Vue.extend({
  data() {
    return {
      url: "",
      show: false,
      devMode: false,
      timeout: 0
    };
  },
  watch: {
    // VueJS watch on vue-router path (only needed for Vue development)
    "$route.path"() {
      this.refreshToken();
    }
  },
  created() {
    const query = qsParams();
    let url = this.$root.$data.ApiOrigin;
    url += "identity/authenticate/";
    url += `?wctx=${window.location.origin + window.location.pathname}`;
    this.url = url;

    if (query.iframe || query.jwt) return;

    this.refreshToken();
    this.$root.$on("web-token", this.refreshToken);

    if (qsParams().dev) this.devMode = true;
  },
  methods: {
    refreshToken() {
      const webToken = localStorage.getItem("web-token");
      const apiToken = localStorage.getItem("api-token");
      let updateApiToken = false;

      if (apiToken) {
        let expires;
        try {
          expires = JSON.parse(apiToken).expires;
        } catch (error) {
          logger.error("Incorrect tokens was removed");
          localStorage.removeItem("web-token");
          localStorage.removeItem("api-token");
          return;
        }

        const expired = dayjs.utc(expires).isBefore(dayjs());

        // REMOVE EXPIRED API TOKEN
        if (expired) {
          logger.error("Expired tokens was removed");
          localStorage.removeItem("web-token");
          localStorage.removeItem("api-token");
          return window.location.reload();
        }

        // UPDATE API TOKEN 10 MINUTES BEFORE EXPIRES
        updateApiToken = dayjs
          .utc(expires)
          .subtract(10, "minute")
          .isBefore(dayjs());
      }

      // ENABLE IFRAME
      if (
        !this.show &&
        !qsParams().iframe &&
        webToken &&
        (!apiToken || updateApiToken)
      ) {
        this.show = true;

        this.$nextTick(() => {
          this.iframeListener();
        });
      }
    },
    iframeListener() {
      const iframe = this.$refs.apiTokenIframe as HTMLElement;
      iframe.addEventListener("load", ev => {
        this.setApiToken(ev);
        clearTimeout(this.timeout);
      });

      if (this.timeout) {
        clearTimeout(this.timeout);
      } else {
        this.timeout = window.setTimeout(() => {
          alertMessage(this, "Login Error: Timeout expired.", this.url);
          if (window.stop) {
            window.stop();
          } else {
            document.execCommand("Stop"); // MSIE
          }
        }, 60000); // 60 seconds
      }
    },
    setApiToken({ target }: any) {
      let jwt;
      try {
        jwt = qsParams(target.contentWindow.location.search).jwt;
      } catch (err) {
        logger.error("Could not set jwt with iframe", this.url, err);
      }
      if (!jwt) return;

      const date = dayjs().add(1, "hour");
      const value = JSON.stringify({ jwt, expires: date.toISOString() });
      localStorage.setItem("api-token", value);

      // DISABLE IFRAME
      this.show = false;

      this.$root.$emit("api-token", value);
    }
  }
});
