












import Vue from "vue";
import { getUserData, someMatch } from "@/lib/utilities";

export default Vue.extend({
  props: {
    requiredRole: { type: String, default: "" }
  },
  data() {
    return {
      hasRequiredRole: !this.requiredRole
    };
  },
  created() {
    this.updateHasRequiredRole();
  },
  methods: {
    updateHasRequiredRole() {
      if (this.hasRequiredRole) return;

      const userData = getUserData();
      if (userData && someMatch(this.requiredRole, userData.Roles)) {
        this.hasRequiredRole = true;
      }
    }
  }
});
