















import Vue from "vue";
import SKIRepository from "@/lib/SKIRepository"

import InfoBox from "@/lib/components/InfoBox.vue"
import { errorHandling, isLoggedIn, qsParams, setHighestPriorityCustomerApplication } from "@/lib/utilities";

interface Data {
  id: string;
  item: {} | null;
  infoDescription: string
}

export default Vue.extend({
  components: { InfoBox },
  data(): Data {
    return { id: "", item: null, infoDescription: "" };
  },
  created() {
    this.id = qsParams().id;
    this.fetchAndSetAgreementAPIData();
  },
  methods: {
    async fetchAndSetAgreementAPIData() {
    var skiRepository = new SKIRepository();
    try{
      var tender = await skiRepository.AgreementsRepository.get(this.id)
      var agreementDescriptions = await skiRepository.AgreementsRepository.getDescription(this.id) ;
      this.$root.$data.itemDescriptions = agreementDescriptions
      if (this.redirectToAgreementPage(tender)) return;
        var ite = tender;
        if(ite) {
          if(isLoggedIn()) {
            setHighestPriorityCustomerApplication(this, ite);
          }
          else {
            ite[this.$config.agreement.application.activeApplication] = null;
          }
        }

      var infoDescriptionRaw = agreementDescriptions
      .find((x: { agreementDescriptionName: string; }) => x.agreementDescriptionName === "Særlig information om aftalen");
      this.infoDescription = infoDescriptionRaw !== undefined ? infoDescriptionRaw.agreementDescriptionText : "";

      this.item = ite;
      this.$root.$data.item = this.item;
    } catch(err) {
      errorHandling(this, err, "/agreement/{id}", this.id);
    }
    },
    redirectToAgreementPage(response: any): boolean {
      var {
        statusField,
        tenderStatus,
        agreementPage
      } = {
        "statusField": "overallStatusName",
        "tenderStatus": "Kommende",
        "agreementPage": "/aftaler/se-aftale/"
      };

      if (response[statusField] !== tenderStatus) {
        window.location.href = agreementPage + "?id=" + this.id;
        return true;
      }
      return false;
    }
  }
});
