





























import Vue from "vue";
import {
  compareValues,
  getFileTypeFromExtension,
  isLoggedIn,
  loginAndRedirect,
  saveValueToLocalStorage
} from "@/lib/utilities";
import SkiModal from "@/lib/components/SkiModal.vue";
import SKIRepository from "@/lib/SKIRepository"
import DocumentCard from "@/lib/components/DocumentCard.vue";
import saveAs from "file-saver";

export default Vue.extend({
  components: { SkiModal, DocumentCard },
  props: {
    contentArray: { type: Array, required: true },
    modalLoginKeys: { type: Object, required: true },
    modalErrorKeys: { type: Object, required: true },
    modalAuthErrorKeys: { type: Object, required: true }
  },
  data() {
    return {
      apiGroupKeys: this.$config.agreement.descriptions.agreementDocuments
    };
  },
  methods: {
    isLoggedIn,
    loginAndRedirect,
    async fetchDocument(document: any) {
      if (!event) return;
      event.preventDefault();

      const keys = this.apiGroupKeys;

      if (!document[keys.loginRequired] || isLoggedIn()) {
      try{
        var repositoryApi = new SKIRepository();
        var response = await repositoryApi.DocumentRepository.getDocument(document[keys.docId])
        saveAs(response.data, `${document.fileName}`);
        this.presentModal(response.status);
      } catch(err){
        const error = err as any;
        this.presentModal(error.statusCode)
      }
      } else {
        this.setElementIdToLocalStorage();
        loginAndRedirect((this.$root.$data.loginUrl), encodeURIComponent(this.modalCallbackUrl()));
      }
     
    },
    presentModal(statusCode: number) {
      switch (statusCode) {
        case 200:
          break;
        case 401:
        case 403:
          this.$bvModal.show(this.modalAuthErrorKeys.modalId);
          break;
        case 999:
          this.$bvModal.show(this.modalLoginKeys.modalId);
          break;
        default:
          this.$bvModal.show(this.modalErrorKeys.modalId);
      }
    },
    getDocProps(docId: string, loginRequired: boolean, authorized: boolean) {
      const docObj = { url: "", label: "", icon: "" };

      if (!loginRequired || authorized) {
        let docUrl = this.$root.$data.ApiOrigin;
        docUrl += this.apiGroupKeys.endpoint_id.replace(":id", docId);

        docObj.url = docUrl;
        docObj.label = "Download";
        docObj.icon = "Download";
      } else if (!isLoggedIn()) {
        docObj.url = "//";
        docObj.label = "Log ind for at downloade";
        docObj.icon = "Lock";
      } else {
        docObj.url = "//";
        docObj.label = "Kræver kundelogin";
        docObj.icon = "Lock";
      }

      return docObj;
    },
    modalCallbackUrl() {
      let url = window.location.pathname;
      url += window.location.search;
      return url;
    },
    documentGroupArray() {
      const documents: any[] = this.contentArray;

      const uniqueDocGroups = [
        ...new Set(documents.map(item => item[this.apiGroupKeys.docType]))
      ];

      const keys = this.apiGroupKeys;

      // Creating new document group objects for each document group
      const docGroupObjects = uniqueDocGroups.map(group => {
        const obj = {
          docGroupName: null,
          docGroupSortKey: null,
          docs: [] as any
        };

        obj.docGroupName = group || "";
        obj.docGroupSortKey = documents.find(
          doc => doc[keys.docType] === group
        )[keys.docGroupSortKey];

        var docsUnsorted = documents.filter(doc => doc[keys.docType] === group);

        // Setting custom attributes on each document
        docsUnsorted.map(doc => {
          doc.fileName = doc.fileName === null ? keys.nameIfNull : doc.fileName;
          doc._fileType = getFileTypeFromExtension(doc[keys.docFileName]);

          const docProps = this.getDocProps(
            doc[keys.docId],
            doc[keys.loginRequired],
            doc[keys.authorized]
          );

          doc._url = docProps.url;
          doc._label = docProps.label;
          doc._icon = docProps.icon;
        });

        // Sorting documents
        obj.docs = docsUnsorted.concat().sort(compareValues(keys.docSortKey));

        return obj;
      });

      const docGroupObjectsSorted = docGroupObjects
        .concat()
        .sort(compareValues(keys.docGroupSortKeyReference));

      return docGroupObjectsSorted;
    },
    setElementIdToLocalStorage(){
      var elementId = {
        agreementDocuments: "aftaledokumenter"
      }
      saveValueToLocalStorage("agreementDocuments", elementId);
    }
  }
});
