




















































import Vue from "vue";
import LinkArrow from "@/lib/assets/icon/link.svg";
import TrafficLight from "@/lib/components/TrafficLight.vue";
import { hasRole, ROLE_CUSTOMER, getUserData, compareValues, isEmpty, isLoggedIn } from "@/lib/utilities";

export default Vue.extend({
  components: { LinkArrow, TrafficLight },
  props: {
    subAgreementsTitle: { type: String, required: true },
    subAgreementsDescription: { type: String, required: true }
  },
  data() {
    return {
      item: this.$root.$data.item,
      isCustomer: false
    };
  },
  computed: {
    filteredSubAgreements() {
      const {
        key,
        number,
        statusFilter
      } = this.$config.agreement.subAgreement;

      var subAgreements = this.$root.$data.item[key];

      var filteredSubAgreements = subAgreements.filter((agr: any) => 
        statusFilter.includes(agr.overallStatusName) || agr.overallStatusName === "Kommende" && agr.showOnAgreement === true 
      );

      return filteredSubAgreements.concat().sort(compareValues(number));
    }
  },
  created() {
    this.isCustomer = this.isUserCustomer();
  },
  methods: {
    isEmpty,
    isLoggedIn,
    getSubAgreementUrl(id: string) {
      return `${window.location.pathname}?id=${id}`;
    },
    isUserCustomer(): boolean {
      const usr = getUserData();
      return hasRole(usr, ROLE_CUSTOMER);
    }
  }
});
