


































import Vue from "vue";
import { qsParams, getUserData, isLoggedIn, btoaUtf } from "@/lib/utilities";
import SkiDefaultButton from "@/lib/components/SkiDefaultButton.vue";
import Catalogue from "@/lib/assets/icon/catalogue.svg";

export default Vue.extend({
  components: { SkiDefaultButton, Catalogue },
  props: {
    viewCatalogueButtonLabel: { type: String, required: true },
    viewCatalogueButtonUrl: { type: String, required: true },
    viewCatalogueButtonTarget: { type: String, default: null },
    orderCatalogueButtonLabel: { type: String, required: true },
    orderCatalogueButtonUrl: { type: String, required: true },
    orderCatalogueButtonTarget: { type: String, default: null },
    greenCatalogueButtonLabel: { type: String, required: true },
    greenCatalogueButtonUrl: { type: String, required: true },
    greenCatalogueButtonTarget: { type: String, default: null }
  },
  data() {
    return {
      showCatalogueButtons: false,
      showOrderCatalogueButton: false,
      showGreenCatalogueButton: false,
      viewCatalogueButtonUrlEncoded: "",
      orderCatalogueButtonUrlEncoded: "",
      greenCatalogueButtonUrlEncoded: ""
    };
  },
  created() {
    const { id } = qsParams();
    const { item } = this.$root.$data;
    const { fullName, catalogue, greenCatalogue, greenFilters } = this.$config.agreement;

    // Hardcoded ski catalogue filter that takes agreement agreement title as parameter
    const filter = {
      Page: 1,
      PageSize: 24,
      SearchTerms: [],
      Sorting: 4,
      Filters: [
        { Id: "aftale", Values: [item[fullName]] }
      ]
    };
    
    const greenFilter = {
      Page: 1,
      PageSize: 24,
      SearchTerms: [],
      Sorting: 4,
      Filters: [
        { Id: "aftale", Values: [item[fullName]] }
      ]
    }

    item.greenFilters.forEach((filter: { filterId: string; filterValues: string[]; }) => {
      greenFilter.Filters.push(
        {
          Id: filter.filterId,
          Values: filter.filterValues
        }
      )
    })

    this.viewCatalogueButtonUrlEncoded = this.viewCatalogueButtonUrl;
    this.viewCatalogueButtonUrlEncoded += `?s=${btoaUtf(JSON.stringify(filter))}`;
    this.orderCatalogueButtonUrlEncoded = `${this.orderCatalogueButtonUrl}?frameworkPartialAgreementKey=${id}`;

    this.greenCatalogueButtonUrlEncoded = this.greenCatalogueButtonUrl;
    this.greenCatalogueButtonUrlEncoded += `?s=${btoaUtf(JSON.stringify(greenFilter))}`;

    if (isLoggedIn()) {
      const userData = getUserData();
      if (userData && userData.Roles.includes(catalogue.requiredRole)) {
        this.showOrderCatalogueButton = true;
      }
    }

    if (item.catalogue) {
      this.showCatalogueButtons = true;
    }

    // Only show green catalogue button if logged in, green catalogue is set to true in CRM and green filters exists for the agreement<
    if (this.showOrderCatalogueButton && item.greenCatalogue && item.greenFilters.length > 0) {
      this.showGreenCatalogueButton = true;
    }
  }
});
