




















import Vue from "vue";
import MySkiSidebar from "@/lib/partials/my-ski/MySkiSidebar.vue";
import SkiModal from "@/lib/components/SkiModal.vue";
import SKIRepository  from '@/lib/SKIRepository';
import {
  isLoggedIn,
  getUserData,
  hasRole,
  ROLE_CUSTOMER,
  ROLE_SUPPLIER
} from "@/lib/utilities";

export default Vue.extend({
  components: { MySkiSidebar, SkiModal },
  props: {
    sidebarContent: { type: Array, required: true }
  },
  data() {
    return {
      org: null,
      show: false,
      callbackUrl: "",
      showHearingWidget: false,
      modalMySKINotLoggedInKeys: {
        modalId: "my-ski-login-modal",
        modalTitle: "Log ind for at se Mit SKI",
        modalText: "Mit SKI er ikke tilgængeligt medmindre du logger ind.",
        modalButtonLabel: "Log ind på Mit SKI",
        modalCancelLabel: "Gå tilbage"
      }
    };
  },
  created() {
    if (isLoggedIn()) {
      const usr = getUserData();
      if (!usr) return;

      if (this.hasCorrectRole(usr)) {
        this.fetchOrganization(usr.OrgId);
      } else {
        window.location.href = this.$root.$data.loginUrl;
      }
    }
  },
  mounted() {
    this.callbackUrl = window.location.pathname;

    if (!isLoggedIn()) {
      this.$bvModal.show(this.modalMySKINotLoggedInKeys.modalId);
    }

    this.$on("show-hearing-widget", (e: boolean) => {
      this.showHearingWidget = e;
    })
  },
  methods: {
    isLoggedIn,
    hasCorrectRole(usr: any): boolean {
      const path = window.location.pathname;

      const role =
        (path.includes("/mit-ski/kunde") && ROLE_CUSTOMER) ||
        (path.includes("/mit-ski/leverandor") && ROLE_SUPPLIER);

      return role && hasRole(usr, role);
    },
    async fetchOrganization(orgId: string) {
      var skiAPiService = new SKIRepository();
      var organization = await skiAPiService.organizationID.get(orgId);
      if (organization) {
        this.org = organization
        this.show = true;
      }
      this.$root.$data.org = this.org;
    }
  }
});
